<template>
    <div class="enterableHome">
        <div>
            <h3>
                <span>我参与的演练项目</span>
            </h3>
            <div class="yet" v-if="!myShow">暂未有我参与的演练项目</div>
            <ul>
                <li v-for="(item,index) in myDrillLists" :key="index" v-if="item.status < 2" :style="'border-bottom: 3px solid ' + randomColor()">
                    <div class="left">
                        <p>演练名称: </p>
                        <p class="enter_detail">演练详情: </p>
                        <p class="enter_time">演练时间: </p>
                    </div>
                    <div class="right">
                        <p>{{ item.name }}</p>
                        <p class="enter_detail">{{ item.description }}</p>
                        <p class="enter_time">{{ item.design_time}}</p>
                    </div>
                    <div class="btn">
                        <el-button @click="startDrill(item.id,item.status)">{{ item.status | myDrillStatus }}</el-button>
                    </div>
                </li>
            </ul>
        </div>
        <div ref="look">
            <h3>
                <span>可观看的演练项目</span>
            </h3>
            <ul>
                <li v-for="(item,index) in drillLists" :key="index" v-if="item.status < 2" :style="'border-bottom: 3px solid ' + randomColor()">
                    <div class="left">
                        <p>演练名称: </p>
                        <p class="enter_detail">演练详情: </p>
                        <p class="enter_time">演练时间: </p>
                    </div>
                    <div class="right">
                        <p>{{ item.name }}</p>
                        <p class="enter_detail">{{ item.description }}</p>
                        <p class="enter_time">{{ item.design_time }}</p>
                    </div>
                    <div class="btn">
                        <el-button @click="lookDrill(item.id,item.status)">{{item.status | lookDrillStatus}}</el-button>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: "enterableHome",
    data() {
        return {
          myDrillLists: [], //我参与的演练的项目
          myShow: false, //是否有我参与的项目
          drillLists: [], //可观看的演练项目
          myId: "" //用户我的id
        };
    },
    methods: {
        randomColor() {
            let colorLists = [
                "#a1eaff",
                "#f2c840",
                "#f89999",
                "#50e3c2",
                "#e38df5",
                "#80c6f7"
            ];
            return colorLists[Math.floor(Math.random() * colorLists.length)];
        },

      //获取我可参与的演练
        getMyRehearse() {
            this.$axios.post("/api/design-me", {
                id: this.myId
            }).then((res)=> {
//                console.log(res);
                let myDrillArr = [];
                for(var i in res.data.data){//获取用户非点评的演练
                    if(res.data.data[i]!=res.data.data.score_user){
	                    myDrillArr.push(res.data.data[i]);
                    }
                }
	            if(res.data.data.score_user.length != 0){//获取用户参与点评的演练
		            for(var i =0; i<res.data.data.score_user.length;i++){
			            myDrillArr.push(res.data.data.score_user[i]);
		            }
	            }
	            console.log(myDrillArr)
	            this.myDrillLists = this.unique(myDrillArr);
                if (res.data.data === null) {
                  //无数据
                    this.myShow = false;
                } else {
                    this.myShow = true;
                }
            }).catch((error)=> {});
        },

      //获取可观看的演练项目
        getWatchRehearse() {
            this.$axios.post("/api/my-look", {
                id: this.myId
            }).then((res)=> {
                //console.log(res);
                this.drillLists = res.data.data;
                if (this.$store.state.status) {
                    window.scrollTo(0, this.$refs.look.offsetTop);
                }
            }).catch((error)=> {});
          },

      //进入演练
        startDrill(val, status) {
//        	console.log(val)
            localStorage.drill_project = val;
            //获取存在localStorage的用户id
            let myId = JSON.parse(localStorage["user"]).id;
            if (status === 0) {//演练还没开始
              //获取用户角色
                this.$axios.post("/api/my-character", {
                    user_id: myId,
                    design_id: val
                }).then((response)=> {
                    //console.log(response)
	                if(response.data.message =='成功'){
		                if(response.data.data.score_user.length!=0){//此演练有点评人,有5种身份
			                if(response.data.data["0"] != undefined){//评分
				                if (response.data.data[0].character === 0) {
					                //导演
					                this.$router.push("/direct");
				                } else if (response.data.data[0].character === 1) {
					                //参演
					                localStorage['roles']='参演';
					                this.$router.push("/Participate");
				                }else if (response.data.data[0].character === 2) {
					                //评分
					                localStorage['roles']='评分';
					                this.$router.push("/Participate");

					                for(var i in response.data.data.score_user){
						                if(response.data.data.score_user[i].score_user==this.myId){
							                localStorage['roles']='评分点评';
							                this.$router.push("/Participate");
							                break;
						                }else{
							                localStorage['roles']='评分';
							                this.$router.push("/Participate");
						                }
					                }
				                }
			                }else{
				                localStorage['roles']='点评';
				                this.$router.push("/Participate");
			                }
		                }else{//此演练无点评人，有3种身份
			                if (response.data.data[0].character === 0) {
				                //导演
				                this.$router.push("/direct");
			                } else if (response.data.data[0].character === 1) {
				                //参演
				                localStorage['roles']='参演';
				                this.$router.push("/Participate");
			                }else if (response.data.data[0].character === 2) {
				                //评分
				                localStorage['roles']='评分';
				                this.$router.push("/Participate");
			                }
		                }
	                }
                }).catch((error)=> {
//                console.log(error)
            });
            } else if (status === 1) {//正在进行的演练
                this.$axios.post("/api/my-character", {
                    user_id: myId,
                    design_id: val
                }).then((response)=> {//practiceStart
//                    console.log(response)
	                if(response.data.message =='成功'){
		                if(response.data.data.score_user!= ''){//此演练有点评人,有5种身份
			               if(response.data.data["0"] != undefined){//评分
				               if (response.data.data[0].character === 0) {
					               //导演
					               this.$router.push("/direct/start");
				               } else if (response.data.data[0].character === 1) {
					               //参演
					               localStorage['roles']='参演';
					               this.$router.push("/practiceStart");
				               }else if (response.data.data[0].character === 2) {
					               //评分
					               localStorage['roles']='评分';
					               this.$router.push("/practiceStart");

					               for(var i in response.data.data.score_user){
					               	    if(response.data.data.score_user[i].score_user==this.myId){
						                    localStorage['roles']='评分点评';
						                    this.$router.push("/practiceStart");
						                    break;
                                        }else{
						                    localStorage['roles']='评分';
						                    this.$router.push("/practiceStart");
                                        }
                                   }
				               }
                           }else{
				                localStorage['roles']='点评';
				                this.$router.push("/practiceStart");
                           }
		                }else{//此演练无点评人，有3种身份
			                if (response.data.data[0].character === 0) {
				                //导演
				                this.$router.push("/direct/start");
			                } else if (response.data.data[0].character === 1) {
				                //参演
				                localStorage['roles']='参演';
				                this.$router.push("/practiceStart");
			                }else if (response.data.data[0].character === 2) {
				                //评分
				                localStorage['roles']='评分';
				                this.$router.push("/practiceStart");
			                }
		                }
	                }
                }).catch((error)=> {
                  console.log(error)
                });
            } else {
              //已经结束的演练
            }
        },

        lookDrill(val,status) {
            localStorage.drill_project = val;
            this.$axios.post("/api/look-status",{
                design_id: val
            }).then((res)=>{
//                 console.log(res)
                 if(status === 1){//演练正在进行
                    this.$router.push("/practiceStart");
                    localStorage['roles']='观看';
                 }
            })
        },

	    //去重
	    unique(array) {
		    var obj = {};
		    return array.filter(function(item, index, array){
			    //console.log(typeof item + JSON.stringify(item))
			    return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
		    })
	    },
    },

    created() {
        this.$store.state.showHeader = "3";
        this.myId = JSON.parse(localStorage["user"]).id;
        this.getMyRehearse();
        this.getWatchRehearse();
    },

    mounted() {
        this.getMyRehearse();
        this.getWatchRehearse();
    },
    filters: {
        myDrillStatus: function(value) {
            if (value == 0) {
                return "开始演练";
            } else if (value == 1) {
                return "正在参加的演练";
            } else if (value == 2) {
               return "演练结束";
            }
        },
        lookDrillStatus: function(value) {
            if (value == 0) {
                return "未开始";
            } else if (value == 1) {
                return "观看演练";
            } else if (value == 2) {
                return "演练结束";
            }
        }
  }
};
</script>

<style lang="less" rel="stylesheet/less">
    .enterableHome {
        width: 1280px;
        margin: 0 auto;
        h3 {
            margin-left: 35px;
            margin-right: 50px;
            margin-top: 15px;
            border-bottom: 1px solid #c2c0c3;
            font-weight: normal;
            height: 40px;
            span {
                height: 39px;
                line-height: 40px;
                display: inline-block;
                font-size: 18px;
                text-align: left;
                color: #4a4a4a;
                border-bottom: 3px solid #4a90e2;
                padding: 0 5px;
            }
        }
        div.yet {
            margin-left: 35px;
            margin-right: 50px;
            margin-top: 30px;
            font-weight: normal;
            height: 40px;
            font-size: 18px;
            text-align: center;
        }
        ul {
            list-style: none;
            margin: 0;
            padding: 35px 0 0;
            li {
                padding: 10px 15px 15px 20px;
                width: 340px;
                font-size: 16px;
                display: inline-block;
                text-align: left;
                /*color: #4a4a4a;*/
                color: #706f70;
                margin-left: 34px;
                margin-bottom: 30px;
                background-color: #ffffff;
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
                .left {
                    display: inline-block;
                    width: 90px;
                }
                .right {
                    float: right;
                    width: 250px;
                }
                p {
                    margin-top: 20px;
                    height: 48px;
                }
                .enter_detail {
                    margin-top: 0;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
                .enter_time{
                    margin-top: 0;
                }
                div.btn {
                    text-align: right;
                    margin-top: 15px;
                    .el-button {
                        height: 34px;
                        color: #fff;
                        border-radius: 100px;
                        background-color: #4a90e2;
                        padding: 9px 20px;
                    }
                }
            }
        }
    }
</style>



